<template>
  <div class="recharge-result-page">
    <div class="recharge-result result-waiting">
      <div class="result-icon"></div>
      <p>待支付</p>
      <p v-if="leftTime">剩余支付时间：{{leftTime}}</p>
    </div>
    <div class="result-list">
      <div><span>充值号码</span><strong>{{mobile | mobileFormate}}</strong></div>
      <div><span>面值</span><strong>￥{{money}}</strong></div>
      <div><span>支付金额</span><strong>￥{{payAmt}}</strong></div>
      <div><span>创建时间</span><strong>{{createTime}}</strong></div>
      <div><span>订单编号</span><strong>{{orderSn}}</strong></div>
    </div>
    <payMethod :mobile="mobile" :money="payAmt" :hasCancel="true" :orderSn="orderSn"/>
    <div class="recharge-tips">温馨提示：本业务不提供发票</div>
  </div>
</template>

<script>
import util from '@/utils/utils'
import URL from '@/http/url'
import payMethod from './components/payMethod'
export default {
  name: 'rechargeresult',
  components: {
    payMethod
  },
  filters: {
    mobileFormate: function (phone) {
      if (!phone) return ''
      let arr = phone.split('')
      let str = ''
      arr.forEach(function(val, ind) {
        if (ind === 3 || ind === 7) {
          str += ' '
        }
        str += val
      })
      return str
    }
  },
  data() {
    return {
      leftTime: 0, // 待支付倒计时
      mobile: '', // 充值号码
      money: '', // 充值金额
      payAmt: '', // 支付金额
      orderSn: '', // 订单号
      createTime: '', // 创建时间
    }
  },
  mounted () {
    this.orderSn = this.$route.query.orderSn
    this.fetchData()
  },
  methods: {
    fetchData() {
      let url = URL.GET_RECHARGE_DETAIL
      // this.intervalLeft(9879)
      this.$http.get(`${url}?orderSn=${this.orderSn}&type=2`).then(res => {
        let result = res.data;

        if (result.success) {
          this.mobile = result.data.billKey
          this.money = result.data.rechargeValue
          this.payAmt = result.data.payAmt
          this.createTime = result.data.orderTime
          this.orderSn = result.data.orderSn
          if (result.data.orderStatus === 'unpaid' && result.data.countDown === -1) {
            this.$toast('订单已过期');
            let timer = setTimeout(() => {
              clearTimeout(timer)
              this.$router.replace('/rechargelist')
            }, 3000)
          } else {
            this.intervalLeft(result.data.countDown)
          }
        } else {
          // this.mobile = '18601583176'
          // this.money = '100'
          // this.createTime = '2020-02-02 11:40:13'
          // this.orderSn = 'L0243323'
          this.$toast(result.message)
        }
      })
    },
    intervalLeft(left) {
      let run = () => {
        if (left < 0) {
          left = 0;
          clearInterval(timer);
          this.$router.replace('/rechargelist')
          return;
        }
        var hour = Math.floor(left / 3600);
        var min = Math.floor((left % 3600) / 60);
        var sec = Math.floor((left % 3600) % 60);
        left--;
        this.leftTime = util.numberToDouble(hour) + ':' + util.numberToDouble(min) + ':' + util.numberToDouble(sec)
      }
      let timer = setInterval(run, 1000);
      run();
    },
  }
}
</script>

